import $ from 'jquery';
/*
 * params: object
 *
 * object.url:   api String 
 * object.type:   String get
 * object.async:  Booleantrue
 * object.data:   object {}
 * */
export default function httpRequest(obj) {
  let async = true; // 
  if (typeof(obj.async) === 'undefined') {
    async = true;
  } else {
    async = Boolean(obj.async);
  }

  const data = obj.data || {};
  return $.ajax({
    url: obj.url,
    type: obj.type || 'GET',
    async: async,
    dataType: obj.dataType || 'json',
    data: data,
  }).done(resp=> {
  }).fail(err => {
  });
};