const tools = {
  addEventHandler: (target, type, fn) => {
    if (target.addEventListener) {
      target.addEventListener(type, fn);
    } else {
      target.attachEvent('on' + type, fn);
    }
  },
  removeEventHandler: (target, type, fn) => {
    if (target.removeEventListener) {
      target.removeEventListener(type, fn);
    } else {
      target.detachEvent('on' + type, fn);
    }
  },
  // url
  getParams: function (url) {
    if (!url) {
      url = window.location.hash;
    }
    const theRequest = {};
    const start = url.indexOf("?");
    if (start !== -1) {
      const str = url.substr(start + 1);
      const strs = str.split("&");
      for (let i = 0; i < strs.length; i++) {
        theRequest[strs[i].split("=")[0]] = strs[i].split("=")[1];
      }
    }
    return theRequest;
  },
  deepClone: (obj) => {     // function
    return JSON.parse(JSON.stringify(obj));
  },
  //  00: 00: 00
  secondToDate: (seconds) => {
    let h = Math.floor(seconds / 3600).toString();
    let m = Math.floor((seconds / 60 % 60)).toString();
    let s = Math.floor((seconds % 60)).toString();
    if (h.length < 2) {
      h = '0' + h;
    }
    ;
    if (m.length < 2) {
      m = '0' + m;
    }
    ;
    if (s.length < 2) {
      s = '0' + s;
    }
    ;
    return h + ':' + m + ':' + s;
  },
  // 
  /*
   * second: 
   * frame 1s -> 
   * */
  secondToFrame: (second, frame = 25) => {
    let h = Math.floor(second / 3600).toString();
    let m = Math.floor((second / 60 % 60)).toString();
    let s = Math.floor((second % 60)).toString();
    const millisecond = second.toFixed(2) - parseInt(second, 10);
    let f = parseInt(millisecond * frame, 10).toString();
    if (h.length < 2) {
      h = '0' + h;
    }
    ;
    if (m.length < 2) {
      m = '0' + m;
    }
    ;
    if (s.length < 2) {
      s = '0' + s;
    }
    ;
    if (f.length < 2) {
      f = '0' + f;
    }
    return h + ':' + m + ':' + s + ':' + f;
  },
  browser: () => {
    const userAgent = navigator.userAgent; //userAgent
    const isOpera = userAgent.indexOf("Opera") > -1;
    if (isOpera) {
      return "Opera"
    }
    //Opera
    if (userAgent.indexOf("Firefox") > -1) {
      return "FF";
    } //Firefox
    if (userAgent.indexOf("Chrome") > -1) {
      return "Chrome";
    }
    if (userAgent.indexOf("Safari") > -1) {
      return "Safari";
    } //Safari
    if (userAgent.indexOf("compatible") > -1 && userAgent.indexOf("MSIE") > -1 && !isOpera) {
      return "IE";
    } //IE
    // // 360
    // if ((window.navigator.userProfile + '') === 'null') {
    //   return "360";
    // }
  },
  sortBy: function (attr, rev) {
    /*
     * key attr : key rev    
     *  newArray.sort(sortBy('number',false))
     * //number; 
     *
     * */
    if (rev === undefined) {
      rev = 1;
    } else {
      rev = (rev) ? 1 : -1;
    }

    return function (a, b) {
      a = a[attr];
      b = b[attr];
      if (a < b) {
        return rev * -1;
      }
      if (a > b) {
        return rev * 1;
      }
      return 0;
    }
  },
  // cookie
  setCookie: (cname, cvalue, exdays) => {
    const d = new Date();
    d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
    const expires = "expires=" + d.toUTCString();
    document.cookie = cname + "=" + cvalue + "; " + expires;
  },
  //cookie
  getCookie: (cname) => {
    const name = cname + "=";
    const ca = document.cookie.split(';');
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) === ' ') c = c.substring(1);
      if (c.indexOf(name) !== -1) return c.substring(name.length, c.length);
    }
    return "";
  },
  // cookie
  clearCookie: (name) => {
    // setCookie(name, "", -1);
  },
  // loading
  show_loading: function () {
    const content_loading = document.querySelector('.content_loading');
    if (content_loading) {
      content_loading.style.display = 'block';
    }
  },
  // loading
  hide_loading: function () {
    const content_loading = document.querySelector('.content_loading');
    if (content_loading) {
      content_loading.style.display = 'none';
    }
  },
  // 
  setUserData_storage: function (userInfo = {}) {
    console.log('userInfo',userInfo)
    localStorage.setItem('dolphin_userInfo', window.encodeURIComponent(JSON.stringify(userInfo)));
  },
  // storage 
  getUserData_storage: function () {
    const userInfo_string_escape = localStorage.getItem('dolphin_userInfo');
    let userInfo = {};
    if (userInfo_string_escape) {
      //  
      let userInfo_string = window.decodeURIComponent(userInfo_string_escape);
      userInfo = JSON.parse(userInfo_string);
    }
    return userInfo;
  },
  //  storage 
  removeUserData_storage: function () {
    localStorage.removeItem('dolphin_userInfo');
  },
  // 
  setCapacity_storage: function (userInfo = {}) {
    console.log('userInfo',userInfo)
    localStorage.setItem('dolphin_capacity', window.encodeURIComponent(JSON.stringify(userInfo)));
  },
  // storage 
  getCapacity_storage: function () {
    const userInfo_string_escape = localStorage.getItem('dolphin_capacity');
    let userInfo = {};
    if (userInfo_string_escape) {
      //  
      let userInfo_string = window.decodeURIComponent(userInfo_string_escape);
      userInfo = JSON.parse(userInfo_string);
    }
    return userInfo;
  },
  //  storage 
  removeCapacity_storage: function () {
    localStorage.removeItem('dolphin_capacity');
  },
};

export default tools;