import React, {Component} from 'react'
import transCode from '@/utils/transCode'


export default
class TransItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      progress: 0,
      isTransing: false, // 
    }

  }

  componentWillMount() {

  }

  componentDidMount() {
    console.log(this.props.data.fileName, 'fileName');
  }

  startTransCode = () => {
    const {fileName, fileSize, fileMd5} = this.props.data;
    const transOptions = {
      inFileName: fileName,  // 
      inFileSize: fileSize,  // 
      inFileMd5: fileMd5,    // md5
      outWidth: '400',     // 
      outHeight: '400',    // 
    };
    transCode({
      transOptions,
      transSuccess: this.transSuccess,    //  
      transFail: this.transFail,       //  
      transProgress: this.transProgress,    //  
    });
    this.setState({
      isTransing: true
    })
  };
  transSuccess = (url) => {
    console.log('transSuccess-->', url);
    this.setState({
      isTransing: false
    })
  };
  transFail = (msg) => {
    console.log(':-->', msg);
    this.setState({
      isTransing: false
    })
  };
  transProgress = (msg) => {
    console.log(':-->' + msg);
    this.setState({
      progress: parseInt(msg) / 100
    })
  };
  render() {
    const {data} = this.props;
    const {progress, isTransing} = this.state;
    return (
      <div className="transItem">
        <div>
          <span>{data.fileName}</span>
          <span>size{data.fileSize}</span>
          <span>MD5{data.fileMd5}</span>
          <button disabled={isTransing} onClick={this.startTransCode}></button>
        </div>

        <progress value={progress} />
      </div>
    )
  }
}